
// Regular Expressions for parsing tags and attributes
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`
const qnameCapture = `((?:${ncname}\\:)?${ncname})`
const startTagOpen = new RegExp(`^<${qnameCapture}`) // ‰ªñÂåπÈÖçÂà∞ÁöÑÂàÜÁªÑÊòØ‰∏Ä‰∏™Ê†áÁ≠æÂêç <xxx ÂåπÈÖçÂà∞ÁöÑÊòØÂºÄÂßã Ê†áÁ≠æÁöÑÂêçÂ≠ó
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`)  // ÂåπÈÖçÁöÑÊòØ</xxx> ÊúÄÁªàÂåπÈÖçÂà∞ÁöÑÂàÜÁªÑÂ∞±ÊòØÁªìÊùüÊ†áÁ≠æÁöÑÂêçÂ≠ó
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/  // ÂåπÈÖçÂ±ûÊÄß
// Â±ûÊÄßÁöÑÁ¨¨‰∏Ä‰∏™ÂàÜÁªÑÂ∞±ÊòØÂ±ûÊÄßÁöÑkey value Â∞±ÊòØÂàÜÁªÑ3/ÂàÜÁªÑ4/ÂàÜÁªÑ5
const startTagClose = /^\s*(\/?)>/ // <div> <br/> Ëá™Èó≠ÂêàÊ†áÁ≠æÁ≠â
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g // ÂåπÈÖçÂà∞ÁöÑÂÜÖÂÆπÂ∞±ÊòØÊàë‰ª¨Ë°®ËææÂºèÁöÑÂèòÈáè
const dynamicArgAttribute = /^\s*((?:v-[\w-]+:|@|:|#)\[[^=]+?\][^\s"'<>\/=]*)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/
const doctype = /^<!DOCTYPE [^>]+>/i
// #7298: escape - to avoid being passed as HTML comment when inlined in page
const comment = /^<!\--/
const conditionalComment = /^<!\[/

export function parseHTML(html) { // html ÊúÄÂºÄÂßãËÇØÂÆöÊòØ‰∏Ä‰∏™<  
  // ÊØèÊ¨°Ëß£ÊûêÂÆå‰∏Ä‰∏™ Ê†áÁ≠æ/Â±ûÊÄß Á≠âÂ∞±Âà†Èô§Êéâ
  function advance(length) {
    html = html.substring(length)
  }

  const ELEMENT_TYPE = 1
  const TEXT_TYPE = 3
  const stack = [] // Áî®‰∫éÂ≠òÊîæÂÖÉÁ¥†Ê†à
  let currentParent // ÊåáÂêëÊ†à‰∏≠ÁöÑÊúÄÂêé‰∏Ä‰∏™
  let root

  // ÊúÄÁªàÈúÄË¶ÅËΩ¨Êç¢Êàê‰∏ÄÈ¢óÊäΩË±°ËØ≠Ê≥ïÊ†ë
  function createASTElement(tag, attrs) {
    return {
      tag,
      attrs,
      type: ELEMENT_TYPE,
      children: [],
      parent: null
    }
  }

  function start(tag, attrs) {
    let node = createASTElement(tag, attrs) // ÂàõÂª∫‰∏Ä‰∏™astËäÇÁÇπ
    if (!root) { // Áúã‰∏Ä‰∏ãÊòØÂê¶ÊòØÁ©∫Ê†ë
      root = node // Â¶ÇÊûúÊòØÁ©∫Ê†ëÂàôÂΩìÂâçÊòØÊ†ëÁöÑÊ†πËäÇÁÇπ
    }

    if (currentParent) {
      node.parent = currentParent // Ëµã‰∫à‰∫Üparent Â±ûÊÄß
      currentParent.children.push(node) // ÂΩìÂâçËäÇÁÇπchildrenÂ¢ûÂä†
    }
    stack.push(node)
    currentParent = node // currentParent ÊòØÊ†à‰∏≠ÁöÑÊúÄÂêé‰∏Ä‰∏™
  }

  function charts(text) {
    // console.log(text); // ÊñáÊú¨Áõ¥Êé•ÊîæÂú®ÂΩìÂâçÊåáÂêëÁöÑËäÇÁÇπ‰∏≠
    text = text.replace(/\s/g, '')
    text && currentParent.children.push({
    // currentParent.children.push({
      type: TEXT_TYPE,
      text,
      parent: currentParent
    })
  }

  function end(tags) {
    // console.log(tags); // ÈÅáÂà∞ÁªìÊùüÊ†áÁ≠æÂøÖÁÑ∂ÊòØstack‰∏≠ÊúÄÂêé‰∏Ä‰∏™ÂÖÉÁ¥†ÔºàÂç≥ÂΩìÂâçÂÖÉÁ¥†ÔºâÁöÑÁªìÊùü
    stack.pop()
    currentParent = stack[stack.length - 1]
  }

  function parsetStartTag() {
    const start = html.match(startTagOpen)

    // console.log(start);
    if (start) {
      const match = {
        tagName: start[1], // Ê†áÁ≠æÂêç
        attrs: []
      }
      advance(start[0].length)
      // Â¶ÇÊûú‰∏çÊòØÂºÄÂßãÊ†áÁ≠æÁöÑÁªìÊùüÂ∞±‰∏ÄÁõ¥ÂåπÈÖç‰∏ãÂéª
      let attr, end;
      while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
        advance(attr[0].length)
        match.attrs.push({name: attr[1], value: attr[3] || attr[4] || attr[5] || true})
      }

      if (end) {
        advance(end[0].length)
      }
      // console.log(match);
      return match
    }
    return false
  }

  while (html) {
    // Â¶ÇÊûútextEnd ‰∏∫0 ËØ¥ÊòéÊòØ‰∏Ä‰∏™ÂºÄÂßãÊ†áÁ≠æÊàñËÄÖÁªìÊùüÊ†áÁ≠æ
    // Â¶ÇÊûútextEnd > 0 ËØ¥ÊòéÊòØÊñáÊú¨ÁöÑÁªìÊùü‰ΩçÁΩÆ
    let textEnd = html.indexOf('<'); // Â¶ÇÊûúindexOf‰∏≠ÁöÑÁ¥¢ÂºïÊòØ0 ÂàôËØ¥ÊòéÊòØ‰∏™Ê†áÁ≠æ

    if (textEnd == 0) {
      const startTagMatch = parsetStartTag();
      if (startTagMatch) {
        start(startTagMatch.tagName, startTagMatch.attrs)
        continue;
      }
      // ÂåπÈÖçÁªìÊùüÊ†áÁ≠æ
      let endTagMatch = html.match(endTag)
      if (endTagMatch) {
        end(endTagMatch[1])
        advance(endTagMatch[0].length)
        continue;
      }
    }
    // ÊñáÊú¨
    if (textEnd > 0) {
      let text = html.substring(0, textEnd); // ÊñáÊú¨ÂÜÖÂÆπ
      if (text) {
        charts(text)
        advance(text.length)
      }
    }
  }
  // console.log(root);

  return root
}
