import Watcher from "./observe/watcher";
import { createElementVNode, createTextVNode } from "./vdom/index"

function createElm(vnode) {
  let { tag, data, children, text } = vnode
  if (typeof tag == 'string') { // Ê†áÁ≠æ
    vnode.el = document.createElement(tag) // ËøôÈáåÂ∞ÜÁúüÂÆûÁöÑËäÇÁÇπÂíåËôöÊãüËäÇÁÇπÂØπÂ∫îËµ∑Êù•, ÂêéÁª≠Â¶ÇÊûú‰øÆÊîπ‰∫ÜÂ±ûÊÄß
    // Â§ÑÁêÜÂ±ûÊÄß
    patchProps(vnode.el, data)
    // ÈÄíÂΩíÊ∑ªÂä†Â≠êËäÇÁÇπdom
    children.forEach(child => {
      vnode.el.appendChild(createElm(child))
    });
  } else { // ÊñáÊú¨ËäÇÁÇπ
    vnode.el = document.createTextNode(text)
  }
  return vnode.el
}

function patchProps(el, props) {
  for (const key in props) {
    if (key == 'style') {
      for (const styleName in props.style) {
        el.style[styleName] = props.style[styleName]
      }
    } else {
      el.setAttribute(key, props[key])
    }
  }
}

export function patch(oldVnode, vnode) {
  const isRealElement = oldVnode.nodeType
  if (isRealElement) {
    // Â¶ÇÊûúÊòØÁúüÂÆûdom (ÂàùÊ∏≤Êüì)
    const elm = oldVnode

    const parenElm = elm.parentNode; // ÊãøÂà∞Áà∂ÂÖÉÁ¥†

    const newElm = createElm(vnode)
    // console.log(newElm)
    /* 
      ÂÖàÊèíÂÖ•Êñ∞ËäÇÁÇπ elÔºåÂÜçÂà†Èô§ËÄÅÁöÑ el ÊòØ‰∏∫‰∫ÜÈÅøÂÖçÂú®ÊèíÂÖ•Êñ∞ËäÇÁÇπ‰πãÂâçÔºå
      ‰∏Ä‰∫õ DOM ÂÖÉÁ¥†Ë¢´ËÑöÊú¨Á≠âÂºÇÊ≠•Êìç‰Ωú‰øÆÊîπÔºåÂØºËá¥Ê∏≤Êüì‰∏çÂáÜÁ°ÆÁöÑÈóÆÈ¢ò„ÄÇ

      Â¶ÇÊûúÂÖàÂà†Èô§ËÄÅÁöÑ el ÂÜçÊèíÂÖ•Êñ∞ÁöÑ elÔºåÈÇ£‰πàÂú®Ëøô‰∏™ËøáÁ®ã‰∏≠ÔºåÂ¶ÇÊûúÂá∫Áé∞‰∫ÜÂºÇÊ≠•Êìç‰ΩúÔºå
      ÂèØËÉΩ‰ºöÂØπËÄÅÁöÑ el ËøõË°å‰øÆÊîπÔºåÂØºËá¥Ê∏≤Êüì‰∏çÂáÜÁ°Æ„ÄÇËÄåÂ¶ÇÊûúÂÖàÊèíÂÖ•Êñ∞ÁöÑ elÔºåÂÜçÂà†Èô§ËÄÅÁöÑ elÔºå
      Â∞±ËÉΩ‰øùËØÅÊñ∞ÁöÑ el ËÉΩÂ§üÊ≠£Â∏∏Ê∏≤ÊüìÔºåÈÅøÂÖç‰∫ÜËøô‰∏™ÈóÆÈ¢ò„ÄÇ
      ÂêåÊó∂ÔºåVue Âú®ÊèíÂÖ•Êñ∞ÁöÑ el Êó∂Ôºå‰πü‰ºö‰øùËØÅÂÆÉÂ§Ñ‰∫éÂêàÈÄÇÁöÑ‰ΩçÁΩÆÔºåÈÅøÂÖç‰∫ÜÊèíÂÖ•‰ΩçÁΩÆÁöÑÈîôËØØ
    */
    parenElm.insertBefore(newElm, elm.nextSibling) // ÂÖàÊèíÂÖ•Êñ∞ÁöÑ el
    parenElm.removeChild(elm) // Âà†Èô§ËÄÅÁöÑ el

    return newElm
  } else {
    // diffÁÆóÊ≥ï
  }
  console.log(vnode)
}

export function initLifeCycle(Vue) {
  Vue.prototype._update = function (vnode) {
    // Â∞ÜËôöÊãüdomËΩ¨Âåñ‰∏∫ÁúüÂÆûdom

    // patch Êó¢ÊúâÂàùÂßãÂåñÁöÑÂäüËÉΩ  ÂèàÊúâÊõ¥Êñ∞ÁöÑÂäüËÉΩ
    const vm = this
    const el = vm.$el

    vm.$el = patch(el, vnode)
    console.log('update', vnode)
  }

  Vue.prototype._c = function () {
    return createElementVNode(this, ...arguments)
  }

  Vue.prototype._v = function () {
    return createTextVNode(this, ...arguments)
  }

  Vue.prototype._s = function (value) {
    if (typeof value !== 'object') return value
    return JSON.stringify(value)
  }

  Vue.prototype._render = function () {
    // console.log(vm.name)
    // ÂΩìÊ∏≤ÊüìÁöÑÊó∂ÂÄô‰ºöÂéªÂÆû‰æã‰∏≠ÂèñÂÄºÔºåÊàë‰ª¨Â∞±ÂèØ‰ª•Â∞ÜÂ±ûÊÄßÂíåËßÜÂõæÁªëÂÆöÂú®‰∏ÄËµ∑
    return this.$options.render.call(this) // ÈÄöËøáastËØ≠Ê≥ïËΩ¨‰πâ
  }
}
// VueÁöÑÊ†∏ÂøÉÊµÅÁ®ã 1) ÂàõÈÄ†‰∫ÜÂìçÂ∫îÂºèÊï∞ÊçÆ 2) Ê®°ÊùøËΩ¨Êç¢ÊàêastËØ≠Ê≥ïÊ†ë 3) Â∞ÜastËØ≠Ê≥ïÊ†ëËΩ¨Êç¢ÊàêrenderÂáΩÊï∞ 
// 4) ÂêéÁª≠ÊØèÊ¨°Êï∞ÊçÆÊõ¥Êñ∞ÂèØ‰ª•Âè™ÊâßË°årender ÂáΩÊï∞(Êó†ÈúÄÂÜçÊ¨°ÊâßË°åastËΩ¨Êç¢ÁöÑËøáÁ®ã)

// render ÂáΩÊï∞‰ºö‰∫ßÁîüËôöÊãüËäÇÁÇπ(‰ΩøÁî®ÂìçÂ∫îÂºèÊï∞ÊçÆ)
// Ê†πÊçÆÁîüÊàêÁöÑËôöÊãüËäÇÁÇπÁîüÊàêÁúüÂÆûDOM
export function mountComponent(vm, el) {
  // console.log(vm)
  vm.$el = el

  // 1„ÄÅË∞ÉÁî®renderÊñπÊ≥ï‰∫ßÁîüËôöÊãüËäÇÁÇπ, ËôöÊãüDOM
  // vm._update(vm._render()) // vm._render()ÂÖ∂ÂÆûÂ∞±ÊòØÊâßË°åÁöÑ vm.render()
  const updateComponent = () => {
    vm._update(vm._render())
  }
  // 2„ÄÅÊ†πÊçÆËôöÊãüDOM‰∫ßÁîüÁúüÂÆûDOM

  // 3„ÄÅÊèíÂÖ•Âà∞elÂÖÉÁ¥†‰∏≠

  // ‰æùËµñÊî∂ÈõÜÁõëÂê¨
  new Watcher(vm, updateComponent, true /* isRenderWatcher */)
}
