// h() _c
export function createElementVNode(vm, tag, data, ...children) {
  if (data == null) {
    data = {}
  }
  // console.log(vm, arg, '----');
  let key = data.key
  if (key) {
    delete data.key
  }
  return vnode(vm, tag, key, data, children)
}

// _v()
export function createTextVNode(vm, text) {
  return vnode(vm, undefined, undefined, undefined, undefined, text)
}

