// Êàë‰ª¨ÁöÑËôöÊãüdom ÊòØÊèèËø∞domÂÖÉÁ¥†ÁöÑÔºå ÂèØ‰ª•Â¢ûÂä†‰∏Ä‰∫õËá™ÂÆö‰πâÂ±ûÊÄßÔºàÊèèËø∞domÁöÑÔºâ
function vnode(vm, tag, key, data, children, text) {
  return {
    vm, tag, key, data, children, text
  }
}
