import { mountComponent } from "./lifecycle";
import { compileToFunction } from "./compiler/index";
import { initState } from "./state";

export function initMixin(Vue) {
  Vue.prototype._init = function (options) {
    console.log(this); // ËøôÈáåÁöÑthisÈÉΩÊòØÂÆû‰æã
    // vm.$optionsÂ∞±ÊòØËé∑ÂèñÁî®Êà∑ÁöÑÈÖçÁΩÆÈÄâÈ°π
    const vm = this
    vm.$options = options // Â∞ÜÁî®Êà∑ÁöÑÈÄâÈ°πÊåÇËΩΩÂà∞ÂÆû‰æã‰∏ä
    // ÂàùÂßãÂåñÁä∂ÊÄÅ
    initState(vm)

    // ÂÆûÁé∞Êï∞ÊçÆÊåÇËΩΩ
    if (options.el) {
      vm.$mount(options.el)
    }
  }

  Vue.prototype.$mount = function (el) {
    const vm = this
    // ÂΩìÂâçÁöÑÊåÇËΩΩÂÖÉÁ¥†
    el = document.querySelector(el)
    let ops = vm.$options
    if (!ops.render) { // ÂÖàÁúãÊúâÊ≤°ÊúâÂÜôrender
      let template;
      if (!ops.template && el) { // Ê≤°ÊúâÂÜôÊ®°Êùø ‰ΩÜÊòØÂÜô‰∫Üel
        template = el.outerHTML
      } else {
        if (el) {
          template = ops.template // Â¶ÇÊûúÊúâel ÂàôÈááÁî®Ê®°ÊùøÁöÑÂÜÖÂÆπ
        }
      }

      // console.log(template);
      // ÂÜô‰∫Ütemplate Â∞±Áî®ÂÜô‰∫ÜÁöÑtemplate
      if (template) {
        // ËøôÈáåÈúÄË¶ÅÂØπÊ®°ÊùøËøõË°åÁºñËØë
        const render = compileToFunction(template);

        ops.render = render // jsx ÊúÄÁªà‰ºöË¢´ÁºñËØëÊàêh('xxx')
      }
    }

    mountComponent(vm, el) // ÁªÑ‰ª∂ÁöÑÊåÇËΩΩ

    // ops.render // ÊúÄÁªàÂèØ‰ª•Ëé∑Âèñrender ÊñπÊ≥ï

    // script Ê†áÁ≠æÂºïÁî®ÁöÑvue.global.js Ëøô‰∏™ÁºñËØëËøáÁ®ãÊòØÂú®ÊµèËßàÂô®ËøõË°åÁöÑ
    // runtime ÊòØ‰∏çÂåÖÂê´ÁºñËØëÁöÑÔºå Êï¥‰∏™ÁºñËØëÊòØÊâìÂåÖÁöÑÊó∂ÂÄôÈÄöËøáloaderÊù•ËΩ¨‰πâ.vue Êñá‰ª∂ÁöÑ
    // Áî®runtimeÁöÑÊó∂ÂÄô‰∏çËÉΩÂú®new VueÈÖçÁΩÆ‰∏≠‰ΩøÁî®template
  }
}

