import { parseHTML } from "./parse";

function genProps(attrs) {
    let str = ''// {name,value}
    for (let i = 0; i < attrs.length; i++) {
        let attr = attrs[i];
        if (attr.name === 'style') {
            // color:red;background:red => {color:'red'}
            let obj = {};
            attr.value.split(';').forEach(item => { // qs Â∫ì
                let [key, value] = item.split(':');
                obj[key] = value;
            });
            attr.value = obj
        }
        str += `${attr.name}:${JSON.stringify(attr.value)},` // a:b,c:d,
    }
    return `{${str.slice(0, -1)}}`
}
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g; // {{ asdsadsa }}  ÂåπÈÖçÂà∞ÁöÑÂÜÖÂÆπÂ∞±ÊòØÊàë‰ª¨Ë°®ËææÂºèÁöÑÂèòÈáè
function gen(node) {
    if (node.type === 1) {
        return codegen(node);
    } else {
        // ÊñáÊú¨
        let text = node.text
        if (!defaultTagRE.test(text)) {
            return `_v(${JSON.stringify(text)})`
        } else {
            //_v( _s(name)+'hello' + _s(name))
            let tokens = [];
            let match;
            defaultTagRE.lastIndex = 0;
            let lastIndex = 0;
            // split
            while (match = defaultTagRE.exec(text)) {
                let index = match.index; // ÂåπÈÖçÁöÑ‰ΩçÁΩÆ  {{name}} hello  {{name}} hello 
                if (index > lastIndex) {
                    tokens.push(JSON.stringify(text.slice(lastIndex, index)))
                }
                tokens.push(`_s(${match[1].trim()})`)
                lastIndex = index + match[0].length
            }
            if (lastIndex < text.length) {
                tokens.push(JSON.stringify(text.slice(lastIndex)))
            }
            return `_v(${tokens.join('+')})`
        }
    }
}
function genChildren(children) {
    return children.map(child => gen(child)).join(',')
}
function codegen(ast) {
    let children = genChildren(ast.children);
    let code = (`_c('${ast.tag}',${ast.attrs.length > 0 ? genProps(ast.attrs) : 'null'
        }${ast.children.length ? `,${children}` : ''
        })`)

    return code;
}
export function compileToFunction(template) {

    // 1.Â∞±ÊòØÂ∞Ütemplate ËΩ¨ÂåñÊàêastËØ≠Ê≥ïÊ†ë
    let ast = parseHTML(template);

    // 2.ÁîüÊàêrenderÊñπÊ≥ï (renderÊñπÊ≥ïÊâßË°åÂêéÁöÑËøîÂõûÁöÑÁªìÊûúÂ∞±ÊòØ ËôöÊãüDOM)

    // Ê®°ÊùøÂºïÊìéÁöÑÂÆûÁé∞ÂéüÁêÜ Â∞±ÊòØ with  + new Function

    let code = codegen(ast);
    code = `with(this){return ${code}}`;
    let render = new Function(code); // Ê†πÊçÆ‰ª£Á†ÅÁîüÊàêrenderÂáΩÊï∞

    //  _c('div',{id:'app'},_c('div',{style:{color:'red'}},  _v(_s(vm.name)+'hello'),_c('span',undefined,  _v(_s(age))))

    return render;
}


